import { useEffect, useRef, useState } from "react";

const UseEffectFn = () => {
  const countref = useRef();
  const [count, setCount] = useState(0);
  useEffect(() => {
    console.log("did mount", count);
  }, []);

  useEffect(() => {
    console.log(" previous value did update", countref.current);
    countref.current = count;
    console.log(" After value did update", countref.current);
  }, [count]);

  useEffect(() => {
    console.log("did update", count);
    return () => {
      console.log("unmount");
    };
  }, [count]);
  const inc = () => {
    setCount(count + 1);
  };
  return (
    <div>
      <h3>{"previous value did update: " + countref.current}</h3>
      <h3>{"count " + count}</h3>
      <button onClick={inc}>inc</button>
    </div>
  );
};
export default UseEffectFn;
